export default {
	data (){
		return {
			Score: ''
		}
	},
	props:{
		headPicShow:{ //图片
			type:[String,Boolean],
			default:true,
		},
		headPicValue:{
			type:String,
			default:require('../static/imgs/st_pic.png')
		},
		
		headTitleShow:{ //标题
			type:[String,Boolean],
			default:true,
		},
		headTitleValue:{
			type:String,
			default:"描述相符"
		},
		
		starsShow:{
			type:[String,Boolean],
			default:true,
		},
		starsMax:{ // 星星最大个数
			type:[String,Number],
			default:5,
		},
		starDefault:{ //未选中
			type:String,
			default:require('../static/imgs/st_star.png'),
		},
		starActive:{
			type:String,
			default:require('../static/imgs/st_star_active.png'),
		},
		score:{  //默认分数
			type:[Number,String],
			default:0
		},
		starsDisabled:{ //是否禁用star
			type:[Boolean],
			default:false
		},
		
		textareaShow:{ // 多行文本显示
			type:[String,Boolean],
			default:true,
		},
		textareaPlaceholder:{
			type:[String],
			default:"宝贝满足你的期待吗？说说你的使用心得，分享给想买的他们吧"
		},
		
		
		submitShow:{ // 发布按钮
			type:[String,Boolean],
			default:true,
		},
		submitText:{
			type:String,
			default:"发布",
		},
		
		infoReceive:{ // 获取值
			type:Object,
			default:function(){
				return {
					score:0,
					textareaValue:""
				}
			}
		}
	},
	computed:{
		// Score(){
		// 	return this.infoReceive.score
		// }
	},
	mounted () {
		if(this.infoReceive.score) {
			this.Score = this.infoReceive.score
		}
	},
	methods: {
		/**
		 * @name 设置分数
		 */
		setScore(score){
			console.log(score)
			if(this.starsDisabled!==false)return
			this.Score=score
			this.$emit("scoreChange",score)
			// console.log(score)
		},
		
		/**
		 * @name 获取textarea内容
		 */
		
		blur(e){
			this.infoReceive.textareaValue=e.detail.value
		},
		
		/**
		 * @name 提交
		 */
		doSubmit(){
			this.$emit('submit',this.infoReceive)
		}
	},
	created() {
		this.infoReceive.score=this.score
	}
}
